// main: ../style.less

.nav {
  display: flex;
  align-items: center;
  height: 100%;
}
.nav__link{
  //padding:  0.66rem 2rem;
  margin-left: 30px;
  position: relative;
  font-size: 0.93rem;
  color: @black;
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none;
  

  &:after {
    content: "";
    width: 0;
    height: 2px;
    background-color: @black;
    position: absolute;
    bottom: -5px;
    left: 0;
    z-index: 1;

    transition: width .2s linear;

  }

  &:hover:not(.nav__link--btn) {
      &:after {
      width: 100%;
    }
  } 
  &--btn {
    padding:  0.66rem 2rem;
    border: 2px solid @black;
    border-radius: 1.2em;

    transition: background-color .2s linear, color .2s linear;

    &:hover {
      background-color: @black; 
      color: @white;
    }
  }
}

